import './Components.css';

function Resource(props) {
    return (
        <>
            <p className='resource'>{props.name}</p>
        </>
    )
}

function Contacts() {
    return (
        <div className='contact-container'>
            <Resource name='GitHub'/>
            <Resource name='Email'/>
            <Resource name='LinkedIn'/>
        </div>
    );
}

export default Contacts;